<template>  
  <div class="map">  
    <!--container-->  
    <div id="allmap"   
        style="  
            width: 96%;   
            height:878px;   
            border: 1px solid gray;  
            overflow:hidden;
            margin: 0 18px;">  
        
    </div>
    <div class="half">
        <dl>
            <dt>半个月定为历史</dt>
            <dd class="clearfix">
                <span class="city">天津</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
            <dd class="clearfix">
                <span class="city">石家庄</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
            <dd class="clearfix">
                <span class="city">唐山</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
            <dd class="clearfix">
                <span class="city">北京</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
            <dd class="clearfix">
                <span class="city">高碑店</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
            <dd class="clearfix">
                <span class="city">廊坊</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
            <dd class="clearfix">
                <span class="city">张家口</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
            <dd class="clearfix">
                <span class="city">沧州</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
            <dd class="clearfix">
                <span class="city">滨州</span>
                <span class="data">2016-11-17  18:52:10</span>
            </dd>
        </dl>
        <el-button type="success" class="btn">下载历史记录</el-button>
    </div>
  </div>  
</template>  
<script>  

export default {  
   
  data: () => ({  
    
  }),  
  created () {  
    // 组件创建完后获取数据，这里和1.0不一样，改成了这个样子  
    this.loadMap()  
    // this.ready() // 如果在此处直接调用this.ready()方法，将无法加载地图  
  },  
  mounted () {  
    this.ready()  
  },  
   methods: {  
    loadMap: function () {  
      console.log(this.$route.params)  
    //   console.log(this.$route.params.addr)  
    //   console.log(this.$route.params.phone)  
      // setTimeout(this.ready, 0)  
    },  
    ready: function () {  
      var map = new BMap.Map('allmap')  
      map.enableScrollWheelZoom(true)  
    //   console.log(map)  
  
      var localSearch = new BMap.LocalSearch(map)  
      // localSearch.enableAutoViewport() // 允许自动调节窗体大小  
      map.clearOverlays() // 清空原来的标注  
      localSearch.setSearchCompleteCallback(function (searchResult) {  
        let poi = searchResult.getPoi(0)  
        map.centerAndZoom(poi.point, 20)  
        let point = new BMap.Point(poi.point.lng, poi.point.lat)  
        let marker = new BMap.Marker(point) // 创建标注，为要查询的地方对应的经纬度  
        map.addOverlay(marker)  
      })  
      localSearch.search(this.$route.params.addr)  
    //   console.log(this.$route.params.addr)
    }  
  }
}  
</script>  
<style  lang="less" scoped>  
    .clearfix{
      content: "";
      display: block;
      clear:both;
    }
  /* 去掉地图左下角的百度LOGO */  
    .anchorBL {  
        display:none; 

    }  
    .map{
        height: 880px;
    }
    .allmap{
        position: relative;
        
    }
    .half{
        width: 300px;
        height: 600px;
        background: #fff;
        position: absolute;
        top:260px;
        right: 180px;
        z-index: 10;
        dl{
            height: 550px;
            dt{
                height: 25px;
                font-size: 18px;
                padding: 10px;
                border-bottom: 1px solid #E9E9E9;
                
            }
            dd{
                height: 25px;
                margin: 10px;
                border-bottom: 1px dashed #E7E7E7;
                .city{
                    float: left;
                }
                .data{
                    float: right;
                    color: #999EB2;
                }
            }
        }
        .btn{
            float: right;
            margin-right: 10px;
        }
    }
</style>  


